@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200&display=swap);

html, body {
    padding: 0px;
    margin: 0px;
    
    font-family: 'Fira Sans', sans-serif;
}

.app {
    --mainColor: #c83e3b;
    --accentColor1: #ffb6b4;
    --accentColor2: #f3d7a0;
    --backgroundColor: #ffffff;
    --textColor: #753333;
    --shadowColor: #0008;
    --doneOpacity: 80%;

    min-height: 100vh;
    background-color: var(--backgroundColor);
}

.button {
    border: 2px solid var(--accentColor2);
    background-color: var(--backgroundColor);
    border-radius: 10px;
    padding: 10px;
    color: var(--textColor);
    transition: background-color 0.2s;
}

.button:active {
    background-color: var(--accentColor1);
}

p {
    color: var(--textColor);
}

.loadingContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}


button, button:active {
    background-color: #0000;
    outline: 0;
    font-size: 120%;
    margin: 10px;
}

.popupContainer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    bottom: 0px;
    left: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--shadowColor);
    color: var(--mainColor);
    z-index: 300;
}

.popupWindow {
    border: none;
    background-color: var(--backgroundColor);
    color: var(--textColor);
    padding: 20px;
    margin: 10px;
    box-sizing: border-box;
    overflow-y: scroll;

    border-radius: 10px;
    border: 2px solid var(--accentColor2);
}
.taskHeadingContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.taskHeading {
    color: var(--textColor);
    margin: 10px;
    padding: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 140%;
    font-weight: 600;
}

.taskDescription {
    color: var(--textColor);
    margin: 10px;
    padding: 0px;
}

.categoryNameContainer {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--backgroundColor);
    padding: 0px;
    margin: 0px;
}

.categoryName {
    color: var(--textColor);
    margin: 10px;
    padding: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 170%;
    font-weight: 600;
}

.task {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    border: 2px solid var(--accentColor2);
    color: var(--textColor);
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: var(--backgroundColor);
}

.taskDone {
    opacity: var(--doneOpacity);
}

.link {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    overflow: hidden;
}

.link svg {
    width: 30px;
    margin-right: 5px;
}

.link p {
    margin: 5px;
}

.taskContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    min-height: 100vh;

    margin: 0px;
    overflow-x: hidden;
    overflow-y: scroll;
    padding-bottom: 30vh;
    box-sizing: border-box;

    background-color: var(--mainColor);
}

.closeTextButton {
    width: 50px;
    height: 50px;
    top: 0px;
    left: 0px;
}

.textContent {
    white-space: pre-wrap;
}

.sidebar {
    z-index: 110;
    position: fixed;
    top: 0px;
    left: 0px;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    max-width: 95vw;
    height: 100vh;
    overflow: scroll;
    margin: 0px;
    padding: 10px;
    padding-bottom: 30vh;
    box-sizing: border-box;
    box-shadow: 0px 0px 20px var(--shadowColor);

    background-color: var(--backgroundColor);
    border-right: 3px solid var(--accentColor2);
    transition: left 0.4s ease, box-shadow 0.4s ease;
}

.hide {
    left: -95vw;
    box-shadow: 0px 0px 0px var(--shadowColor);
}

.sidebarHeading {
    font-size: 175%;
    margin: 15px 10px;
    color: var(--textColor);
}

.sidebarButton {
    white-space: nowrap;
    font-size: 120%;
    margin: 10px 5px;
    color: var(--textColor);
    display: flex;
    flex-direction: column;
    border: none;
}

.sidebarHeading {
    white-space: nowrap;
    font-weight: 600;
}

.buttonGroup {
    padding-left: 5px;
    margin-left: 10px;
    border-left: 2px solid var(--accentColor1);
}

.sidebarButton:active {
    color: var(--accentColor1);
}

.toggleSidebarButton {
    box-shadow: 0px 0px 20px var(--shadowColor);
    z-index: 120;
    position: fixed;
    left: 0px;
    bottom: 0px;
    margin: 20px;
    width: 50px;
    height: 50px;
}

.settings {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    padding-bottom: 20vh;
}

.settingsHeading {
    color: var(--textColor);
    border-bottom: 3px solid var(--accentColor1);
    margin: 40px;
}

.optionHeading {
    font-size: 150%;
    margin: 5px;
}

.options {
    padding-left: 10px;
    margin-bottom: 40px;
    margin-left: 10px;
    border-left: 2px solid var(--accentColor1);
}

.option {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.option button {
    margin: 5px;
    border: none;
    color: var(--textColor);
    font-size: 120%;
}

.option button:active {
    color: var(--accentColor1);
}

.option div {
    width: 10px;
    height: 10px;
    margin: 0px;
}

.selected {
    border-radius: 5px;
    background-color: var(--mainColor);
}

.buttonContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.frontpage {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 20vh;
}

.frontpage button {
    z-index: 90;
}

.frontpagebg {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
}

.frontpageHeading {
    z-index: 50;
    height: 55vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
}

.frontpageHeading p {
    color: #ffffff;
    font-size: 240%;
    margin: 5px;
}

.introContainer {
    font-size: 140%;;
    margin: 10px;
    padding: 10px;
}


.svg {
    overflow: hidden;
    margin: 0px;
    padding: 0px;
}

.fillAccent1 {
    fill-opacity: 0.7;
    fill: var(--accentColor1);
    stroke: none;
}

.fillAccent2 {
    fill-opacity: 0.7;
    fill: var(--accentColor2);
    stroke: none;
}

.fillMain {
    fill: var(--mainColor);
    stroke: none;
}

.fillDark {
    fill: var(--textColor);
    stroke: none;
}

.strokeDark {
    fill: none;
    stroke: var(--textColor);
}
